.film-gallery__container {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;

    @include mq(tablet) {
        padding-left: 32px;
        padding-right: 32px;
        width: 768px;
    }

    @include mq(desktop) {
        padding-left: 32px;
        padding-right: 32px;
        width: 1280px;
    }
}
